



















































































































import { Component, Vue } from 'vue-property-decorator'
import Axios from '@/Axios'
import Swal from 'sweetalert2'
import AnimacionCargando from '../../../components/animacionCargando.vue'

@Component({
  components: {
    AnimacionCargando,
  },
})
export default class Login extends Vue {
  estadoPeticion = true
  listaInscripciones = []
  paginate = ['listaInscripciones']
  filter = ''
  fecha_pic = '2021'
  fechas_pic = []
  listaAdjudicados: any = []
  id_persona = null
  adjudicar = false
  nombre = ''
  convocatoria = {
    convocatoria: '',
    fecha_inicio: '',
    fecha_final: '',
    lugar: '',
    perfiles_requisitos: '',
    financiacion: '',
    id_pic_convocatoria: '',
    para_funcionarios: '',
    pertenece_a_red: 'SI', //dato quemado
    pic_id: '',
    plan_institucional_id: '',
    id_pic: '',
    pic_fase_id: 0,
    id_usuario_autenticado: '',
    nacional: '',
    id_municipio: '',
  }

  constructor() {
    super()
    const dict = {
      messages: {
        required: () => 'Campo obligatorio',
        min: () => 'Mínimo 8 caracteres',
        // email: () => "Correo invalido",
      },
    }
    this.$validator.localize('es', dict)
  }

  mounted() {
    this.obtenerPic(this.$route.params.id)
    this.obtenerInscritos()
  }

  obtenerInscritos() {
    let id = this.$route.params.id
    Axios()
      .get('listar_inscripciones_pic/' + id)
      .then((respuesta) => {
        this.listaInscripciones = respuesta.data.results
        this.estadoPeticion = false
      })
  }

  obtenerPic(id) {
    Axios()
      .get('editar_convocatoria_pic/' + id)
      .then((respuesta) => {
        if (respuesta.data.status == 'success') {
          this.convocatoria = respuesta.data.results
        } else {
          Swal.fire({
            title: 'Hay un problema',
            text: respuesta.data.message,
            icon: 'error',
          })
          this.$router.push('GestionarConvocatoriaENI')
        }
      })
  }
  cambiarEstadoConvocatoria() {
    const id_usuario = JSON.parse(sessionStorage.getItem('usuario'))
    Axios()
      .put('/actualizar_fase_pic/' + this.$route.params.id, {
        id_pic_convocatoria: this.$route.params.id,
        pic_fase_id: 3,
        id_usuario_actualiza: id_usuario.id_usuario,
      })
      .then(() => {
        Swal.fire({
          title: 'Convocatoria ',
          text: this.convocatoria.convocatoria +","+ ' pasó a fase de Validación.',
          confirmButtonText: 'Aceptar',
          confirmButtonColor: '#238276',
          icon: 'success',
        })
        this.$router.back()
      })
      .catch(() => {
        Swal.fire({
          title: 'ERROR',
        })
      })
  }

  filtrar(lista: any[]) {
    let resp
    if (this.filter != '') {
      resp = lista.filter(
        (item) =>
          item.capacitacion
            .substring(0, this.filter.length)
            .toLowerCase()
            .normalize('NFD')
            .replace(/[\u0300-\u036f]/g, '') === this.filter.toLowerCase(),
      )
    } else {
      resp = lista
    }
    return resp
  }
  ordenar(n: number, type: string) {
    let rows,
      switching,
      i,
      x,
      y,
      shouldSwitch,
      dir,
      switchcount = 0

    const table: any = document.getElementById('listado')
    switching = true
    dir = 'asc'

    while (switching) {
      switching = false
      rows = table.rows
      for (i = 1; i < rows.length - 1; i++) {
        shouldSwitch = false
        x = rows[i].getElementsByTagName('TD')[n]
        y = rows[i + 1].getElementsByTagName('TD')[n]
        if (dir == 'asc') {
          if (
            (type == 'str' &&
              x.innerHTML.toLowerCase() > y.innerHTML.toLowerCase()) ||
            (type == 'int' && parseFloat(x.innerHTML) > parseFloat(y.innerHTML))
          ) {
            shouldSwitch = true
            break
          }
        } else if (dir == 'desc') {
          if (
            (type == 'str' &&
              x.innerHTML.toLowerCase() < y.innerHTML.toLowerCase()) ||
            (type == 'int' && parseFloat(x.innerHTML) < parseFloat(y.innerHTML))
          ) {
            shouldSwitch = true
            break
          }
        }
      }
      if (shouldSwitch) {
        rows[i].parentNode.insertBefore(rows[i + 1], rows[i])
        switching = true
        switchcount++
      } else {
        if (switchcount == 0 && dir == 'asc') {
          dir = 'desc'
          switching = true
        }
      }
    }
  }
}
