



























































































































































































































































































































































































































































































import Axios from "@/Axios";
import Component from "vue-class-component";
import { Vue } from "vue-property-decorator";
import Swal from "sweetalert2";
import XLSX from "xlsx";
import BarChart from "@/components/Charts/BarChart";
import AnimacionTablasCargando from "@/components/animacionCargando.vue";
import Chart from "chart.js";

@Component({
  components: {
    AnimacionTablasCargando,
    BarChart,
  },
})
export default class listaConvocatoriasNacional extends Vue {
  canvas = document.getElementsByTagName("canvas");
  verGraficas = false;
  listaRegionales = [];
  listaConvocatoriasBienestar = [];
  listaConvocatoriasBienestarFiltrado = [];
  listaApoyos = [];
  listaCentros = [];
  listaFases = [];
  filterFase = "";
  filterApoyo = "";
  filterCentro = "";
  contConvCreacion = 0;
  contConvCancelacion = 0;
  contConvFinalizada = 0;
  contConvEjecucion = 0;
  contConvAdjudicacion = 0;
  contConvInscripcion = 0;
  contConvCierre = 0;
  estadoPeticionTabla=false;
  contApoyoSostenimientoF = 0;
  contApoyoSostenimientoR = 0;
  contRepresentantes = 0;
  contReporteActividades = 0;
  contMonitorias = 0;
  contInternados = 0;
  contApoyoTransporte = 0;
  contApoyoAlimentacion = 0;

  paginate = ["listaConvocatoriasBienestarFiltrado"];
  regional = {
    id_regional: null,
    regional: null,
  };

  cantidadActividades = 0;
  listaActividades = [];
  Creacion = 0;
  postulacion = 0;
  validacion = 0;
  Terminacion = 0;
  finalizada = 0;

  mounted() {
    this.obtenerRegionales();
    this.obtenerConvocatoriasBienestar();
    this.obtenerApoyos();
    this.obtenerFases();
  }
  obtenerRegionales() {
    Axios()
      .get("listar_regionales")
      .then((respuesta) => {
        this.listaRegionales = respuesta.data.results;
      })
      .catch(() => {
        Swal.fire(
          "ERROR!",
          "Se ha presentado un error en el servidor!",
          "error"
        );
      });
  }
  obtenerApoyos() {
    Axios()
      .get("crear_tipos_apoyo")
      .then((respuesta) => {
        this.listaApoyos = respuesta.data.results.tipos_apoyo;
      })
      .catch(() => {
        Swal.fire(
          "ERROR!",
          "Se ha presentado un error en el servidor!",
          "error"
        );
      });
  }
  obtenerCentrosRegional(id_regional) {
    Axios()
      .get("listar_centro_formacion_regional/ " + id_regional)
      .then((respuesta) => {
        this.listaCentros = respuesta.data.results;
      })
      .catch(() => {
        Swal.fire(
          "ERROR!",
          "Se ha presentado un error en el servidor!",
          "error"
        );
      });
  }
  obtenerFases() {
    Axios()
      .get("crear_convocatoria")
      .then((respuesta) => {
        this.listaFases = respuesta.data.results.fases_bienestar;
      })
      .catch(() => {
        Swal.fire(
          "ERROR!",
          "Se ha presentado un error en el servidor!",
          "error"
        );
      });
  }
  obtenerConvocatoriasBienestar() {
    Axios()
      .get("listar_vista_Bienestar_Convocatorias")
      .then((respuesta) => {
        if (respuesta.data.status.toLowerCase() == "success") {
          this.listaConvocatoriasBienestar = respuesta.data.results;
          this.listaConvocatoriasBienestarFiltrado = respuesta.data.results;
          this.generarContadores();
          this.estadoPeticionTabla = true;

        } else {
          Swal.fire({
            title: "Hubo un error obteniendo las convocatorias",
            icon: respuesta.data.status,
            text: respuesta.data.message,
            confirmButtonText: "Aceptar",
          });
        }
      })
      .catch(() => {
        Swal.fire(
          "ERROR!",
          "Se ha presentado un error en el servidor!",
          "error"
        );
      })
      .finally(() => {
          this.estadoPeticionTabla = true;
        });  
  }
  async generarContadores() {
    for (let i of this.listaConvocatoriasBienestar) {
      if (i.tipo_apoyo == "Apoyos de alimentación") {
        this.contApoyoAlimentacion = this.contApoyoAlimentacion + 1;
      } else if (i.tipo_apoyo == "Apoyos de transporte") {
        this.contApoyoTransporte = this.contApoyoTransporte + 1;
      } else if (i.tipo_apoyo == "Internados-Centros de convivencia") {
        this.contInternados = this.contInternados + 1;
      } else if (i.tipo_apoyo == "Monitorias") {
        this.contMonitorias = this.contMonitorias + 1;
      } else if (
        i.tipo_apoyo ==
        "Reporte de actividades de la política de enfoque pluralista y diferencial"
      ) {
        this.contReporteActividades = this.contReporteActividades + 1;
      } else if (i.tipo_apoyo == "Representantes de aprendices") {
        this.contRepresentantes = this.contRepresentantes + 1;
      } else if (i.tipo_apoyo == "Apoyos de sostenimiento Regular") {
        this.contApoyoSostenimientoR = this.contApoyoSostenimientoR + 1;
      } else if (i.tipo_apoyo == "Apoyos de sostenimiento FIC") {
        this.contApoyoSostenimientoF = this.contApoyoSostenimientoF + 1;
      }
    }

    for (let i of this.listaConvocatoriasBienestar) {
      if (i.fase_bienestar == "Creación") {
        this.contConvCreacion = this.contConvCreacion + 1;
      } else if (i.fase_bienestar == "Cancelación") {
        this.contConvCancelacion = this.contConvCancelacion + 1;
      } else if (i.fase_bienestar == "Finalizada") {
        this.contConvFinalizada = this.contConvFinalizada + 1;
      } else if (i.fase_bienestar == "Ejecución") {
        this.contConvEjecucion = this.contConvEjecucion + 1;
      } else if (i.fase_bienestar == "Adjudicación") {
        this.contConvAdjudicacion = this.contConvAdjudicacion + 1;
      } else if (i.fase_bienestar == "Inscripción") {
        this.contConvInscripcion = this.contConvInscripcion + 1;
      } else if (i.fase_bienestar == "Cierre") {
        this.contConvCierre = this.contConvCierre + 1;
      }
    }
    this.graficas();
  }
  graficas() {
    new Chart(this.canvas[0], {
      type: "line",
      data: {
        labels: [
          "Creación",
          "Cancelación",
          "Finalizada",
          "Ejecución",
          "Adjudicación",
          "Inscripción",
        ],
        datasets: [
          {
            label: "Fases",
            data: [
              this.contConvCreacion,
              this.contConvCancelacion,
              this.contConvFinalizada,
              this.contConvEjecucion,
              this.contConvAdjudicacion,
              this.contConvInscripcion,
            ],
            backgroundColor: [
              "rgba(255, 99, 132, 0.2)",
              "rgba(255, 159, 64, 0.2)",
              "rgba(255, 205, 86, 0.2)",
              "rgba(75, 192, 192, 0.2)",
              "rgba(54, 162, 235, 0.2)",
              "rgba(153, 102, 255, 0.2)",
            ],
            borderColor: [
              "rgb(255, 99, 132)",
              "rgb(255, 159, 64)",
              "rgb(255, 205, 86)",
              "rgb(75, 192, 192)",
              "rgb(54, 162, 235)",
              "rgb(153, 102, 255)",
              "rgb(201, 203, 207)",
            ],
            borderWidth: 1,
          },
        ],
      },
    });
    new Chart(this.canvas[1], {
      type: "bar",
      data: {
        labels: [
          "Apoyos de alimentación",
          "Apoyos de transporte",
          "Internados-Centros de convivencia",
          "Monitorias",
          "Reporte de actividades de la política de enfoque pluralista y diferencial",
          "Representantes de aprendices",
          "Apoyos de sostenimiento Regular",
          "Apoyos de sostenimiento FIC",
        ],
        datasets: [
          {
            label: "Apoyos",
            data: [
              this.contApoyoAlimentacion,
              this.contApoyoTransporte,
              this.contInternados,
              this.contMonitorias,
              this.contReporteActividades,
              this.contRepresentantes,
              this.contApoyoSostenimientoR,
              this.contApoyoSostenimientoF,
            ],
            backgroundColor: [
              "rgba(255, 99, 132, 0.2)",
              "rgba(255, 159, 64, 0.2)",
              "rgba(255, 205, 86, 0.2)",
              "rgba(75, 192, 192, 0.2)",
              "rgba(54, 162, 235, 0.2)",
              "rgba(153, 102, 255, 0.2)",
              "rgba(153, 102, 255, 0.2)",
              "rgba(153, 102, 255, 0.2)",
            ],
            borderColor: [
              "rgb(255, 99, 132)",
              "rgb(255, 159, 64)",
              "rgb(255, 205, 86)",
              "rgb(75, 192, 192)",
              "rgb(54, 162, 235)",
              "rgb(153, 102, 255)",
              "rgb(201, 203, 207)",
              "rgb(201, 203, 207)",
            ],
            borderWidth: 1,
          },
        ],
      },
    });
  }
  filtroFases() {
    let arregloFiltrado = [];

    for (let conv of this.listaConvocatoriasBienestar) {
      let fase = conv.fase_bienestar;
      if (fase.indexOf(this.filterFase) >= 0) {
        arregloFiltrado.push(conv);
      }
      this.listaConvocatoriasBienestarFiltrado = arregloFiltrado;
    }
    if (this.listaConvocatoriasBienestarFiltrado.length == 0) {
      //busco no encontro
      Swal.fire({
        title: "No hay coincidencias",
        position: "center",
        timer: 1000,
        text: "No se encontró convocatorias que coincidan con las busqueda",
        showConfirmButton: false,
        // confirmButtonText: "Aceptar",
        // confirmButtonColor: "#238276",
        backdrop: "rgba(0,0,0,0)",
        background: "#eeeeee",
      });
      this.filterFase = "";
      this.listaConvocatoriasBienestarFiltrado =
        this.listaConvocatoriasBienestar;
    }
  }
  filtroApoyo() {
    let arregloFiltrado = [];

    for (let conv of this.listaConvocatoriasBienestar) {
      let apoyo = conv.tipo_apoyo;
      if (apoyo.indexOf(this.filterApoyo) >= 0) {
        arregloFiltrado.push(conv);
      }
      this.listaConvocatoriasBienestarFiltrado = arregloFiltrado;
    }
    if (this.listaConvocatoriasBienestarFiltrado.length == 0) {
      //busco no encontro
      Swal.fire({
        title: "No hay coincidencias",
        position: "center",
        timer: 1000,
        text: "No se encontró convocatorias que coincidan con las busqueda",
        showConfirmButton: false,
        // confirmButtonText: "Aceptar",
        // confirmButtonColor: "#238276",
        backdrop: "rgba(0,0,0,0)",
        background: "#eeeeee",
      });
      this.filterApoyo = "";
      this.listaConvocatoriasBienestarFiltrado =
        this.listaConvocatoriasBienestar;
    }
  }
  filtroRegional() {
    let arregloFiltrado = [];

    for (let conv of this.listaConvocatoriasBienestar) {
      let regional = conv.regional;
      if (regional.indexOf(this.regional.regional) >= 0) {
        arregloFiltrado.push(conv);
      }
      this.listaConvocatoriasBienestarFiltrado = arregloFiltrado;
    }
    if (this.listaConvocatoriasBienestarFiltrado.length == 0) {
      //busco no encontro
      Swal.fire({
        title: "No hay coincidencias",
        position: "center",
        timer: 1000,
        text: "No se encontró convocatorias que coincidan con las busqueda",
        showConfirmButton: false,
        // confirmButtonText: "Aceptar",
        // confirmButtonColor: "#238276",
        backdrop: "rgba(0,0,0,0)",
        background: "#eeeeee",
      });
      this.regional = null;
      this.listaConvocatoriasBienestarFiltrado =
        this.listaConvocatoriasBienestar;
    }
  }
  filtroCentro() {
    let arregloFiltrado = [];

    for (let conv of this.listaConvocatoriasBienestar) {
      let centro = conv.centro_formacion;
      if (centro.indexOf(this.filterCentro) >= 0) {
        arregloFiltrado.push(conv);
      }
      this.listaConvocatoriasBienestarFiltrado = arregloFiltrado;
    }
    if (this.listaConvocatoriasBienestarFiltrado.length == 0) {
      //busco no encontro
      Swal.fire({
        title: "No hay coincidencias",
        position: "center",
        timer: 1000,
        text: "No se encontró convocatorias que coincidan con las busqueda",
        showConfirmButton: false,
        // confirmButtonText: "Aceptar",
        // confirmButtonColor: "#238276",
        backdrop: "rgba(0,0,0,0)",
        background: "#eeeeee",
      });
      this.filterCentro = null;
      this.listaConvocatoriasBienestarFiltrado =
        this.listaConvocatoriasBienestar;
    }
  }
  filtroNacional() {
    this.listaConvocatoriasBienestarFiltrado = this.listaConvocatoriasBienestar;
  }
  exportExcel() {
    let data = XLSX.utils.json_to_sheet(
      this.listaConvocatoriasBienestarFiltrado,
      {
        header: [
          "convocatoria",
          "regional",
          "centro_formacion",
          "tipo_apoyo",
          "fase_bienestar",
        ],
      }
    );
    (data["A1"].v = "convocatoria"),
      (data["B1"].v = "regional"),
      (data["C1"].v = "centro formacion"),
      (data["D1"].v = "tipo apoyo"),
      (data["E1"].v = "fase bienestar");

    const workbook = XLSX.utils.book_new();
    const filename =
      "Reporte Convocatorias " +
      new Date().getFullYear() +
      "-" +
      (new Date().getMonth() + 1);
    XLSX.utils.book_append_sheet(workbook, data, filename);
    XLSX.writeFile(workbook, `${filename}.xlsx`);
  }
}
